/* stylelint-disable font-family-no-missing-generic-family-keyword */

@import '../../../../sass/mixin.scss';

._menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

@media (min-width: 992px) {

  ._menu {
    padding: 0;
  }
}

.messages,
.notifications {
  @include headerNavigationBarMenuLabelBadge;

  &Icon {
    padding: 0;
  }
}

@media (min-width: 992px) {

  .messages,
.notifications {

    &Icon {
      padding: 0 12px 0 0;
    }
  }
}

.placeAd {
  @include headerNavigationBarMenuButtonBadge;

  &Icon {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
    -webkit-filter: FlipH;
            filter: FlipH;
    color: #fff;
    padding: 0;
  }
}

@media (min-width: 992px) {

  .placeAd {

    &Icon {
      padding: 0 8px;
    }
  }
}

.placeAd {

  &Button {
    font-family: BreeSerif;
    font-size: 14px;
    -ms-flex-item-align: center;
        align-self: center;
  }
}

.messagesLabel,
.notificationsLabel,
.placeAdButton {
  display: none;
}

@media (min-width: 992px) {

  .messagesLabel,
.notificationsLabel,
.placeAdButton {
    display: block;
  }
}

/* stylelint-enable font-family-no-missing-generic-family-keyword */
