@import '../../../../sass/mixin.scss';

.dropdown {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.summary:focus {
  background-color: #e7f0f7;
  outline: 0 auto;
}

.badge {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 4px;

  @include headerNavigationBarMenuLabelBadge;
}

.svgIcon1,
.label,
.svgIcon2 {
  padding: 0 3px;
}

.label,
.svgIcon2 {
  display: none;
}

@media (min-width: 992px) {

  .label,
.svgIcon2 {
    display: block;
  }
}

.list {
  position: absolute;
  background: #fff;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .14);
          box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .14);
  list-style-type: none;
  margin: 0;
  overflow: hidden;
  padding: 0;

  &Item {
    white-space: nowrap;
    padding: 8px 63px 8px 16px;
    color: #116db4;
    border-bottom: 1px solid #e7f0f7
  }

  &Item:hover {
    color: #1589e3;
    background-color: #e7f0f7;
    cursor: pointer;
  }
}
