.maxWidth {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 20px 0;
  margin: 0 auto;
  max-width: 1010px;
  min-width: 288px;
}

.topBanner {
  width: 100%;
  padding-left: 24px;
}

@media (min-width: 950px) {

  .topBanner {
    width: 81.7%;
  }
}

.leftColumn {
  background-color: #f0efed;
  min-width: 192.8px;
  width: 19.28%;
}

@media (min-width: 950px) {

  .leftColumn {
    margin-top: 0;
  }
}
