.navigationBar {
  background-color: #fff;
}

.maxWidth {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 16px 8px;
}

@media (min-width: 992px) {

  .maxWidth {
    padding: 12px 0;
  }
}

.maxWidth {
  margin: 0 auto;
  max-width: 980px;
  min-width: 288px;

  &Left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    &Logo {
      padding: 0 16px 0 0;
    }

    &Links {
      display: none;
    }
  }
}

@media (min-width: 768px) {

  .maxWidth {

    &Left {

      &Links {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
      }
    }
  }
}
