@import '../../../../sass/mixin';

.mapLink {
  &Heading {
    &Anchor {
      color: #2d3c4d;

      @include leftColumnTitle;
    }
  }

  &Image {
    border-radius: 33px;
    -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .14);
            box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .14);
    cursor: pointer;
    width: 100%;
    max-width: 190px;
  }
}
